import { dengluwcAPI, mrgqAPI } from "@/api";
import bus from "@/eventBus";
export default {
  data() {
    return {
      mgs: 6350765658,
      name: '',
      tp: '',
      zxmwList: []
    };
  },

  async mounted() {
    setTimeout(function () {
      bus.$on("czx", val => {
        this.mgs = val;
      });
    }, 1000);
    const res = await dengluwcAPI({
      uid: this.mgs
    });
    console.log("名称", res.data.profile);
    this.name = res.data.profile.nickname;
    this.tp = res.data.profile.avatarUrl; //每日歌曲

    const ress = await mrgqAPI();
    console.log('每日歌曲', ress.data.data);
    this.zxmwList = ress.data.data;
  }

};