import bus from "@/eventBus";
import { dengluAPI } from "@/api";
export default {
  data() {
    return {
      shuju: 200,
      name: 18745333245,
      password: 'a201925010219',
      id: 1
    };
  },

  methods: {
    async dl() {
      const res = await dengluAPI({
        phone: this.name,
        password: this.password
      });
      setTimeout(function () {
        bus.$emit('czx', this.id);
        this.id = res.data.account.id;
      }, 2000);
      this.shuju = res.data.code;
      console.log('当前账号id', res.data.account.id);
      console.log("当前账号", this.name);
      console.log("当前密码", this.password);
    }

  }
};