import Vue from "vue";
import VueRouter from "vue-router";
Vue.use(VueRouter)
import Layout from '@/views/Layout'
import Home from '@/views/Home'
import Search from '@/views/Search'
import play from "@/views/Play"
import Wode from "@/views/Wode"
import Denglu from "@/views/Denglu"
import Dengluwc from "@/views/Debgluwc"

const router = new VueRouter({
    routes: [
        { path: '/', redirect: '/Layout/Home' },
        {
            path: '/layout',
            component: Layout,
            children: [{
                    path: 'home',
                    component: Home,
                    meta: { // meta保存路由对象额外信息的
                        title: "首页"
                    }
                },
                {
                    path: 'search',
                    component: Search,
                    meta: { // meta保存路由对象额外信息的
                        title: "搜索"
                    }
                },
                {
                    path: 'wode',
                    component: Wode,
                    redirect: '/Layout/Wode/Denglu',
                    meta: { // meta保存路由对象额外信息的
                        title: "我的"
                    },
                    children: [
                        { path: 'denglu', component: Denglu },
                        { path: 'dengluwc', component: Dengluwc },
                    ]
                }
            ]
        },
        {
            path: "/play",
            component: play
        }
    ]
})

router.beforeEach(function(to, from, next) {

    if (to.path === '/layout/Wode/Dengluwc') {
        setTimeout(function() {
            const token = from.matched[2].instances.default.shuju
            console.log('bbb', from.matched[2].instances.default.shuju);
            console.log(from.matched[1].redirect);
            if (token === 200) {
                next()
                from.matched[1].redirect = '/Layout/Wode/Dengluwc'
            } else {
                alert("网易云-账号或密码不对啊 铁汁！！！")
                next('/layout/Wode/Denglu')
            }
        }, 2000)

    } else {
        next()
    }


})
export default router